@import url(https://fonts.googleapis.com/css?family=Josefin+Slab:100,100i,300,300i,400,400i,600,600i,700,700i);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family:'Josefin Slab';
  font-size: 16px;

  color: #FDBB07;

  background-color: #222222;

  overflow-y: scroll;
  overflow-x: hidden;

  border: 0;
  margin: 0;
}

a {
  text-decoration: none;
  transition-duration: 0.3s;
}

main{
  position: absolute;

  height: 100%;
  width: calc(100% - 700px);

  top: 0;
  left: 700px;

  margin: 0;
  border: 0;
}

.container{ margin: 40px }

.circle-bottom{
  z-index: -3000;
  position: fixed;

  width: 800px;
  height: 800px;

  bottom: 0;
  right: 0;

  margin-right: -400px;
  margin-bottom: -400px;
  border-radius: 50%;
  background: #FFF8E6;
}

.align-right{ float: right }

@media (max-width: 870px){
  .circle-bottom{
    display: none;
  }
  main{
    position: inherit;
    width: 100%!important;
  }
}

@media (max-width: 1360px){
  main{
    left: 400px;
    width: calc(100% - 400px);
  }
}

figure.background-image{
    position: fixed;
  
    width: 650px;
    height: 100%;
  
    top: 0;
    left: 0;
  
    margin: 0;
    border: 0;
  }
  
  figure.background-image img{
    width: 100%;
    height: 100%;
  
    object-fit: cover;
  }
  
  @media (max-width: 1360px){
    figure.background-image{
      width: 400px;
    }
  }
  
  @media (max-width: 870px){
    figure.background-image{
      position:inherit;
      display: block;
      width: 100%;
      height: 100;
  
    }
  }
ul.socials {
  list-style: none;
  padding-left: 20;
}

ul.socials li {
  display: inline-block;
  margin: 0 13px;
}

ul.socials li a {
  font-size: 20px;
  color: #fdbb07;
}

ul.socials li a i {
  transition-duration: 0.2s;
}
ul.socials li a i:hover {
  -webkit-transform: scale(3);
          transform: scale(3);
}

nav.menu {
  margin: 25px;
}

nav.menu ul {
  display: inline-block;
  list-style: none;
  padding-left: 0;
}

nav.menu ul.links li {
  display: inline-block;
  margin: 0 15px;
}

nav.menu ul.links li a {
  position: relative;
  display: inline-block;

  font-size: 25px;
  font-weight: 500;
  letter-spacing: 1px;
  text-decoration: none;
  color: #fdbb07;

  padding: 0px 5px;

  transition: all 0.5s;
}

nav.menu ul.links li a::before {
  z-index: -1;
  position: absolute;
  display: block;

  content: " ";

  margin: 0 auto;
  height: 100%;
  width: 0%;
  bottom: 0px;
  left: 0%;

  transition: all 0.5s;
}

nav.menu ul.links li a.active::before {
  z-index: -1;
  position: absolute;
  display: block;

  content: " ";

  margin: 0 auto;
  height: 100%;
  width: 100%;
  bottom: 0px;
}

nav.menu ul.links li a:hover::before {
  width: 100%;
  height: 110%;
  left: 0%;
  background-color: #a43b25;
}

